.header {
  background-color: $white;
  padding: 12px 0;
  &__wrapper {
    padding: 0 20px;
    display: flex;
    justify-content: space-between; } }

.main-nav {
  &__list {
    display: flex;
    align-items: center;
    margin: 0 -30px; }
  &__item {
    padding: 0 30px; }
  &__link {
    color: $mainTextColor;
    font-size: 10px;
    line-height: 1.2; } }

.logo {
  @include flex-center;
  &__text {
    margin-left: 10px;
    color: $mainTextColor;
    font-family: $montserrat;
    font-size: 15px;
    font-weight: 600;
    line-height: 1.533;
    letter-spacing: -0.04em;
    .logo_white & {
      color: $white; }
    .logo_big & {
      font-size: 23px;
      margin-left: 18px; } }
  &__image {
    display: block;
    max-width: 100%;
    .logo_big & {
      height: 35px; } } }
